import { isFunction } from '../../utils/type'
import { getRootApp, getConfig } from '../app/config'
import { addRoutes } from '../router/index'

/**
 * init
 * @param {import("./map").SubAppConfig} appConfig
 */
export function init({ init, routes, parentPath }) {
  const { parentPath: globalParentPath } = getConfig()

  return Promise.resolve(isFunction(init) ? init(getRootApp()) : true).then(
    () => {
      // @ts-ignore
      addRoutes(routes, parentPath || globalParentPath)
    }
  )
}
